.article {
  max-width: 740px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  margin: 0 auto; }
  .article--container {
    padding: 2rem 0rem;
    border-bottom: var(--line); }
  .article img {
    background-size: cover;
    max-width: 740px; }
  .article__title {
    font-size: 3rem;
    letter-spacing: -1px;
    line-height: 1.4; }
  .article__summary {
    font-size: 1.5rem;
    letter-spacing: -1px;
    margin-bottom: 2rem;
    font-style: italic;
    color: var(--color-grey-dark-3);
    font-weight: 300; }
  .article__content > div {
    font-size: 1.3rem;
    display: flex;
    align-items: center;
    flex-direction: column; }
  .article__related__posts {
    background: #f9f9f9;
    padding: 2rem; }
    .article__related__posts .blog__posts {
      max-width: 75rem;
      margin: 0 auto; }
  .article__paragraph {
    margin-bottom: 2rem; }
  .article__line-height--2 {
    line-height: 2 !important; }
