.blog {
  max-width: 75rem;
  margin: 0 auto; }
  .blog__feature {
    padding: 2rem; }
    .blog__feature--container {
      height: 60vh;
      background: #f9f9f9;
      display: flex;
      justify-content: center;
      overflow: hidden; }
    .blog__feature__content {
      flex: 0 0 40%;
      padding: 4rem;
      align-self: center;
      cursor: pointer; }
    .blog__feature__title {
      display: inline-block;
      padding: 1rem 0rem; }
      .blog__feature__title .highlight__hover {
        background-image: linear-gradient(120deg, #ffc400c2 0%, #ffc400c2 100%);
        background-repeat: no-repeat;
        background-position: 0 88%;
        background-size: 100% 0;
        transition: background-size 0.25s ease-in; }
      .blog__feature__title:hover .highlight__hover {
        background-size: 100% 0.5rem; }
    .blog__feature__desc {
      color: #3b3b3b;
      font-weight: 400;
      font-size: 1rem;
      padding-bottom: 0.5rem; }
    .blog__feature__image-box {
      width: 100%;
      cursor: pointer; }
      .blog__feature__image-box:hover {
        filter: opacity(0.85); }
    .blog__feature__image {
      height: 100%;
      background-position: 50% 50%;
      background-size: cover; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: "Roboto"; }
